import React, { Component } from 'react'
import Pokemon from '../components/pokemon'
import Search from '../components/search'

var Alert = require('react-bootstrap').Alert;
class Page extends Component {
  componentDidMount() {
    this.props.getPokemons()
  }

  handleSearch(event) {
    this.props.filterPokemons(event.currentTarget.value)
  }

  render() {
    let { displayedPokemons, isFetched, error } = this.props

    let pokemons = displayedPokemons.map(pokemon => {
      return (
        <div className="row">
        <div className="col-md-6 pokemons__item" key={pokemon.id}>
        
          <div className="card re-card">
          <img className="card-img-top" src="https://media.istockphoto.com/photos/hot-homemade-pepperoni-pizza-picture-id521403691?k=6&amp;m=521403691&amp;s=612x612&amp;w=0&amp;h=644DsEXT1-dsizk8_0pYiYtq4aluKcRuxd4t2Q0lp3k=" alt="Card image" style={{width:100+'%'}}/>
          <div className="card-body">
          <h4 className="card-title">Special Cheeze Pizza</h4>
          <p className="card-text">This is a special cheeze pizza made with corn</p>
          <span className="rating">Rating: 5+</span>
          <a href="#" className="btn btn-primary edit-btn">Edit</a>
          </div>
          </div>
          <div className = "indi-box" style = {{width:41 + '%', backgroundColor:`white`}}>
            <div className = "indi-title">Ingredients</div>
            <ul class = "indi-list" type = "none">
              <li className = "indi-item">Ginger<span className="indi-quant">2gm</span></li>
              <li className = "indi-item">Salt<span className="indi-quant">5tsp</span></li>
              <li className = "indi-item">Red Chilli<span className="indi-quant">2ml</span></li>
              <li className = "indi-item">Pepper<span className="indi-quant">6 cups</span></li>
            </ul>
            <span className = "seemore">See More</span>
          </div>
        </div>
        </div>
      )
    })

    return (
      <div className="page">
       <div className="container-fluid">
        {error && <div className="page__error">{error}</div>}
        <div className="page__search">
          <Search onChange={this.handleSearch.bind(this)} />
        </div>
        {isFetched ? (
          <p>Loading...</p>
        ) : (
          <ul className="pokemons">{pokemons}</ul>
        )}
      </div>
      </div>
    )
  }
}

export default Page



